@import url(https://fonts.googleapis.com/css2?family=Style+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
.top {
  width: 100%;
  height: 50px;
  background-color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  opacity: 0.93;
  margin-top: 15px;
  z-index: 999;
}

.topLeft,
.topRight {
  flex: 3 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.topIcon {
  font-size: 30px;
  margin-right: 10px;
  color: #444;
  cursor: pointer;
}

.topCenter {
  font-family: "Josefin Sans", sans-serif;
  flex: 6 1;
}

.topList {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.topListItem {
  margin-right: 20px;
  font-size: 25px;
  font-weight: 300;
  cursor: pointer;
}

.topListItem:hover {
  color: rgb(129, 129, 129);
}

.topImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 15px;
  cursor: pointer;
}
.toplink{
  color: rgb(219, 106, 41);
  font-family: fantasy;
}
.toplink:hover{
  color: rgb(233, 89, 5);
  text-shadow: 0 0 0.2px black;
}
.topSearchIcon {
  font-size: 18px;
  color: #666;
  cursor: pointer;
}

.header {
  margin-top: 60px;
}

::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  background: white;
}

