  font-family: 'Style Script', cursive;
  top: 18%;
  font-size: 26px;
}

.headerTitleLg {
  position: absolute;
  top: 20%;
  color: black;
  text-shadow: 0px 0px 7px black;
  font-size: 100px;
}

.headerTitleLg b{
  color: aliceblue;
}

.headerImg {
  width: 100%;
  height: 450px;
  margin-top: 80px;
